@charset "utf-8";

/****************************************************
	Reset CSS
*****************************************************/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    font-family: 'Bree Serif', 'Kosugi', sans-serif;
    color: #fff;
    vertical-align: baseline;
}

/****************************************************
	HTML5 display-role reset for older browsers
*****************************************************/
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/****************************************************
	HTML5 display-role reset for older browsers
*****************************************************/

body {
  background-color: #222222;
}

/****************************************************
	General
*****************************************************/

body,
.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

p, a, th, td {
  font-size: 0.7rem;
}

.wrapper {
  margin: 0 auto;
  width: 90%;
}

.page-title {
  border-left: 5px solid #444444;
  color: #eeeeee;
  font-size: 1.6rem;
  margin: 20px auto 30px auto;
  padding-left: 10px;
}

.break {
  display: inline-block;
}

.mt {
  margin-top: 10px;
}

@media only screen and (min-width: 576px) {
  p, a, th, td {
    font-size: 0.8rem;
  }
}

@media only screen and (min-width: 768px) {
  p, a, th, td {
    font-size: 0.8rem;
  }
}

@media only screen and (min-width: 992px) {
  .wrapper {
    width: 798px;
  }

  .page-title {
    font-size: 2.0rem;
  }
}

/****************************************************
	Header
*****************************************************/

.logo {
  color: #dbcbbd;
  font-family: 'Lobster', cursive;
  font-size: 2.5rem;
  margin: 85px auto 15px auto;
  text-align: center;
}

.page-wrapper {
  background-color: #444444;
  opacity: 0.7;
  position: absolute;
  top: 140px;
  left: 0;
  width: 100vw;
}

.page-list {
  margin: 0 auto;
  width: 800px;
}

.link-menu {
  border-left: 1px dashed #888888;
  display: inline-block;
  font-size: 1.2rem;
  padding: 16px 0;
  text-align: center;
  width: 114px;
}
.link-menu:last-child {
  border-right: 1px dashed #888888;
}
.link-menu:hover{
  color: #222222;
  background-color: #eeeeee;
  text-decoration: none;
  opacity: 1;
}

@media only screen and (max-width: 449px) {
  .logo {
    font-size: 2.2rem;
  }
}

@media only screen and (min-width: 450px) {
  .logo {
    margin-top: 50px;
  }
}

@media only screen and (max-width: 991px) {
  .page-wrapper {
    display: none;
  }
}

@media only screen and (min-width: 992px) {
  .burger-button{
    display: none;
  }
  .logo {
    font-size: 3.2rem;
    margin-bottom: 125px;
  }
}

/****************************************************
	Home
*****************************************************/

.slide-container {
  display: table;
  margin: 10px 0 40px 0;
  table-layout: fixed;
  width: 100%;
}

.slide-pic {
  width: 100%
}

.slick-dots li button:before {
  color: #eeeeee;
}

.slick-dots li.slick-active button:before {
  color: #ffffff;
}

.shop-explanation {
  line-height: 1.1rem;
}

/****************************************************
	Menu
*****************************************************/

.menu-title {
  border-bottom: 1px solid #ffffff;
  font-size: 1.2rem;
  margin: 20px 0 5px 0;
  padding-bottom: 7px;
}

.menu-name {
  padding: 5px 0;
  text-align: left;
  width: 100%;
}

.price {
  font-size: 0.9rem;
  text-align: right;
}

.menu-description,
.explanation {
  color: #999999;
  font-size: 0.7rem;
}

.menu-description {
  line-height: 1rem;
  padding: 0 0 0 16px;
}
.menu-description:last-child {
  padding-bottom: 8px;
}

.explanation {
  text-align: right;
  margin-top: 15px;
}

/****************************************************
	Stylist
*****************************************************/

.pic-left {
  max-width: 150px;
  width: 40%;
}

.profile {
  margin-top: 30px;
}

.comment:nth-of-type(1) {
  margin-top: 20px;
}

.english-name {
  font-size: 0.9rem;
}

.stylist-comment {
  line-height: 1.1rem;
}

@media only screen and (min-width: 768px) {
  .pic-left {
    float: left;
  }

  .profile {
    float: left;
    margin: 0 0 0 30px;
    width: 60%;
  }
  .profile::after {
    clear: both;
  }
}

/****************************************************
	Reserve
*****************************************************/

.reserve {
  line-height: 1.1rem;
}

.btn {
  border: 1px solid #ffffff;
  color: #ffffff;
  display: block;
  margin: 50px auto 15px auto;
  padding: 0;
}

.btn:visited {
  color: #ffffff;
}

.link-reserve {
  display: block;
  background-color: #333333;
  line-height: 2.5rem;
  width: 150px;
}

.link-reserve:hover {
  background-color: #eeeeee;
  color: #222222;
  text-decoration: none;
}

.caution {
  font-size: 0.7rem;
  text-align: center;
}

/****************************************************
	Access
*****************************************************/

.google-map {
  height: 400px;
  width: 100%;
}

.pin {
  align-items: center;
  color: #ff0000;
  display: flex;
  position: relative;
  width: 180px;
}

.pin-icon {
  font-size: 1.5rem;
  position: absolute;
  top: -20px;
  left: -10px;
}

.pin-text {
  color: #ff0000;
  font-family: 'Kosugi Maru', sans-serif;
  font-size: 0.9rem;
  font-weight: bold;
}

.marker:hover {
  z-index: 1;
}

.access {
  line-height: 1.1rem;
  margin: 20px 0 0 0;
}

.address {
    margin-top: 30px;
}

/****************************************************
	About / Reqruit
*****************************************************/

.table-about,
.table-reqruit {
  table-layout: fixed;
  width: 100%;
}

.th-about, .td-about, .th-reqruit, .td-reqruit  {
  border: 2px solid #222222;
  padding: 10px 15px;
}

.th-about, .th-reqruit  {
  background-color: #eeeeee;
  color: #222222;
}

.td-about, .td-reqruit {
  background-color: #333333;
  line-height: 1.4rem;
}

@media only screen and (max-width: 767px) {
  .th-about, .td-about, .th-reqruit, .td-reqruit  {
    display: block;
  }
}

@media only screen and (min-width: 768px) {
  .th-about {
    width: 215px;
  }
  .th-reqruit {
    width: 160px;
  }
}

/****************************************************
	Footer
*****************************************************/

footer {
  margin-top: auto;
  padding: 30px 0 20px 0;
}

.copyright, .tel {
  line-height: 1.3rem;
  text-align: center;
}

.link-tel {
  text-decoration: underline;
}

.link-tel:hover {
  color: #ffffff;
}

.link-tel:visited {
  color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .copyright, .tel {
    font-size: 0.7rem;
  }
}

/****************************************************
	End
*****************************************************/
